.Popup__smile-mimic{
  position: absolute;
  left: 4.3vw;
  width: 7vw;
  min-height: 4vw;
  border-top: .7vw solid;
  -webkit-animation: smile .8s ease-out 4s 1 normal both running;
    animation: smile .8s ease-out 4s 1 normal both running;
}

@keyframes smile {
  from {border-radius: 35%;}
  25% {border-radius: 40%;}
  50% {border-radius: 45%;}
  to {border-radius: 50%;}
  }